import React from 'react';

import Tools from 'services/Tools/main';

import './main.scss';


interface Props
{
	data: any;
}
const Content_: React.FC<Props> = (props) =>
{
	const _data = props.data;

	function boxs()
	{
		return <div className="boxs">
			{_data.map((val: any, idx: number) => {
				return <div className="box" key={idx}>
					<img src="./assets/img/sample.png" alt=""/>
					<button className="photo tools" onClick={Tools.wiggle}>
					</button>
					<p className="title">{val.title}</p>
				</div>;
			})}
		</div>;
	}

	return <div id="show-window-content" className="content">
		{boxs()}
	</div>
}

const Content = React.memo(Content_, () => true);
export default Content;
