import React, { useRef } from 'react';

import './main.scss';


const SearchBar1_: React.FC = () =>
{
	console.log(SearchBar1_.name);

	const _iptRef = useRef(null);

	function focus()
	{
		let ipt:any = _iptRef.current;
		ipt.focus();
	}

	return <div id="search-bar1">
		<div className="contents" onClick={focus}>
			<input ref={_iptRef}
				   type="text"
				   placeholder="(내 취향 검색)" />
		</div>
	</div>
}

const SearchBar1 = React.memo(SearchBar1_, () => true);
export default SearchBar1;
