import React from 'react';
import './TicTacToe.css';


const lines = [
  [0, 1, 2],
  [3, 4, 5],
  [6, 7, 8],
  [0, 3, 6],
  [1, 4, 7],
  [2, 5, 8],
  [0, 4, 8],
  [2, 4, 6]
];


interface Props {}
interface State {
  history: Array<{squares: Array<string>}>;
  stepNumber: number;
  xIsNext: boolean;
}
class TicTacToe extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      history: [{squares: Array(lines.length).fill(null)}],
      stepNumber: 0,
      xIsNext: true
    };
  }

  calculateWinner(squares: Array<string>) {
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (squares[a] &&
          squares[a] === squares[b] &&
          squares[a] === squares[c]) {
        return squares[a];
      }
    }
    return null;
  }

  handleClick(i: number) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = history[history.length - 1];
    const squares = current.squares.slice();
    if (this.calculateWinner(squares) || squares[i]) {
      return;
    }
    squares[i] = this.state.xIsNext ? "X" : "O";
    this.setState({
      history: history.concat([{squares: squares}]),
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext
    });
  }

  jumpTo(step: number) {
    this.setState({
      stepNumber: step,
      xIsNext: (step % 2) === 0
    });
  }

  renderSquare(idx: number) {
    return (
      <button className="square"
              onClick={() => this.handleClick(idx)}>
        {this.state.history[this.state.stepNumber].squares[idx]}
      </button>
    );
  }

  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner = this.calculateWinner(current.squares);

    const moves = history.map((_, move) => (
      <li key={move}>
        <button onClick={() => this.jumpTo(move)}>
          {move ? 'Go to move #' + move : 'Go to game start'}
        </button>
      </li>
    ));

    let status = winner ? "Winner: " + winner :
                          "Next player: " + (this.state.xIsNext ? "X" : "O");
    return (
      <div className="game">
        <div className="game-board">
          <div>
            <div className="board-row">
              {this.renderSquare(0)}
              {this.renderSquare(1)}
              {this.renderSquare(2)}
            </div>
            <div className="board-row">
              {this.renderSquare(3)}
              {this.renderSquare(4)}
              {this.renderSquare(5)}
            </div>
            <div className="board-row">
              {this.renderSquare(6)}
              {this.renderSquare(7)}
              {this.renderSquare(8)}
            </div>
          </div>
        </div>
        <div className="game-info">
          <div>{status}</div>
          <ol>{moves}</ol>
        </div>
      </div>
    );
  }
}

export default TicTacToe;
