import React, { useRef } from 'react';

import './main.scss';


const SearchBar_: React.FC = () =>
{
	console.log(SearchBar_.name);

	const _iptRef = useRef(null);

	function focus()
	{
		let ipt:any = _iptRef.current;
		ipt.focus();
	}

	return <div id="search-bar">
		<div className="contents" onClick={focus}>
			<input ref={_iptRef} type="text" placeholder="검색어 (모월 연)" />
			<img className="search-icon" src="./assets/icon/search.png" alt="" />
		</div>
	</div>
}

const SearchBar = React.memo(SearchBar_, () => true);
export default SearchBar;
