import React from 'react';

import Tools from 'services/Tools/main';

import './main.scss';


const CommonList_: React.FC = () =>
{
	console.log(CommonList_.name);

	function contents(num: number)
	{
		const contents_: any = [];
		for (let idx = 0; idx < num; idx++)
		{
			let content = <div className="content" key={idx}>
				<div className="scroll-line"></div>
				<div className="img">
					<img src="./assets/img/sample2.png" alt=""/>
				</div>
				<div className="profile">
					<span className="name1">운곡도가</span>
					<span className="name2">토끼구름 막걸리</span>
					<span className="score">★ 4.2</span>
					<span className="price">11,000원~</span>
				</div>
				<div className="panel">
					<span className="review">
						맛있어요! 우유와 요거트 맛의 조화 어쩌구 저쩌구 일단 아무튼 짱!
					</span>
					<span className="reviewer">
						<img src="./assets/icon/avatar.png" alt="" /> makguli88
					</span>
				</div>
				<button className="card tools" onClick={Tools.wiggle}>
				</button>
				<button className="heart tools" onClick={Tools.wiggle}>
					<img src="./assets/icon/heart.png" alt=""/>
				</button>
			</div>;
			contents_.push(content);
		}
		return contents_
	}

	return <div id="common-list">{contents(10)}</div>;
}

const CommonList = React.memo(CommonList_, () => true);
export default CommonList;
