import React, { useRef, useEffect } from 'react';
import { IonApp,
         IonRouterOutlet,
         IonTabBar,
         IonTabs,
		 IonButton,
		 IonTabButton} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { Redirect, Route } from 'react-router-dom';

import MainPage from './pages/MainPage/main';
import MyPage from './pages/MyPage/main';
import Tab2 from './pages/Tab2';
import Tab4 from './pages/Tab4';
import RedCarpet from './components/RedCarpet/main';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';
/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';
/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';
/* Theme variables */
import './theme/variables.css';

import './theme/custom.css';
import './theme/navi.scss';


const App: React.FC = () =>
{
	console.log(App.name);

	let _idx = 1;
	const _links: Array<any> = [];
	const _imgDataRef = useRef(null);
	const _btns: Array<any> = [];
	const _btnsRef = useRef(null);

	useEffect(() => {
		const btns = document.getElementsByTagName("ion-tab-button");
		Array.from(btns).forEach(val => _links.push(val));
		const ele: any = _btnsRef.current;
		Array.from(ele.children).forEach((val, idx) => {
			if (idx !== 2)
			{
				_btns.push(val)
			}
		});

	});

	function basename()
	{
		const base = window.document.getElementsByTagName("base")[0]
					 				.getAttribute("href");
		return base ? base : "/";
	}

	function shot()
	{
		const ele: any = _imgDataRef.current;
		ele.click();
	}

	function relocate(idx: number)
	{
		_links[idx].click();
		_btns[_idx].classList.remove("selected");
		_btns[idx].classList.add("selected");
		_idx = idx;
	}

	return <IonApp>
		<RedCarpet />
		<IonReactRouter basename={basename()}>
			<IonTabs>
				<IonRouterOutlet>
					<Route path="/tab2" component={Tab2} exact />
					<Route path="/MainPage" component={MainPage} exact />
					<Route path="/tab4" component={Tab4} exact />
					<Route path="/MyPage" component={MyPage} exact />
					<Redirect from="/" to="/MainPage" exact />
				</IonRouterOutlet>
				<IonTabBar slot="bottom">
					<IonTabButton tab="tab2" href="/tab2"></IonTabButton>
					<IonTabButton tab="MainPage" href="/MainPage"></IonTabButton>
					<IonTabButton tab="tab4" href="/tab4"></IonTabButton>
					<IonTabButton tab="MyPage" href="/MyPage"></IonTabButton>
				</IonTabBar>
			</IonTabs>
		</IonReactRouter>

		<div className="status-bar"></div>
		<div className="data-container">
			<input ref={_imgDataRef}
      			   className="img-data"
				   type="file"
				   accept="image/*"
				   capture="environment" />
		</div>
		<div className="tab-bar" ref={_btnsRef}>
			<IonButton color="tab" onClick={() => relocate(0)}>
				<img src="./assets/icon/feed.png" alt=""/>
			</IonButton>
			<IonButton color="tab" onClick={() => relocate(1)}>
				<img src="./assets/icon/main.png" alt=""/>
			</IonButton>
			<IonButton className="camera" color="tab" onClick={shot}>
				<img src="./assets/icon/camera.png" alt=""/>
			</IonButton>
			<IonButton color="tab" onClick={() => relocate(2)}>
				<img src="./assets/icon/bag.png" alt=""/>
			</IonButton>
			<IonButton color="tab" onClick={() => relocate(3)}>
				<img src="./assets/icon/profile.png" alt=""/>
			</IonButton>
		</div>
	</IonApp>;
}

export default App;
