import React, { useRef, useEffect } from 'react';

import './main.scss';

import Content from "./Content/main"

import db from "db/category.json";


const ShowWindow_: React.FC = () =>
{
	console.log(ShowWindow_.name);

	const _contentsRef = useRef(null);
	const _contents: Array<any> = [];
	const _btnsRef = useRef(null);
	const _btns: Array<any> = [];
	const _barRef = useRef(null);
	const _cases = db.cases;
	const _datas = db.datas;
	let _idx = 0;

	useEffect(() => {
		let ele: any;
		ele = _contentsRef.current;
		Array.from(ele.children).forEach(val => _contents.push(val));
		_contents[0].classList.add("selected");
		ele = _btnsRef.current;
		Array.from(ele.children).forEach(val => _btns.push(val));
		_btns[0].classList.add("selected");
		ele = _barRef.current;
		ele.classList.add("case0");
	});

	function relocate(idx: number)
	{
		let content = _contents[idx];
		if (idx === _idx)
		{
			return;
		}
		content.scrollLeft = 0;
		content.classList.add("selected");
		_contents[_idx].classList.remove("selected");
		_btns[_idx].classList.remove("selected");
		_btns[idx].classList.add("selected");
		let bar: any = _barRef.current;
		bar.classList.remove("case" + _idx);
		bar.classList.add("case" + idx);
		_idx = idx;
	}

	function btns()
	{
		return _cases.map((case_, idx) => {
			return <button key={idx}
							className="btn"
							onClick={() => relocate(idx)}>
				{case_}
			</button>;
		});
	}

	function contents()
	{
		return _datas.map((data, idx) => <Content key={idx} data={data} />);
	}

	return <div id="show-window">
		<div className="focus-bar">
			<div className="bar" ref={_barRef}></div>
		</div>
		<div className="btns" ref={_btnsRef}>{btns()}</div>
		<div className="contents" ref={_contentsRef}>{contents()}</div>
		<div className="scroll-hider"></div>
	</div>;
}

const ShowWindow = React.memo(ShowWindow_, () => true);
export default ShowWindow;
