import React, { useEffect, useRef } from 'react';

import './main.scss';


const RedCarpet_: React.FC = () =>
{
	console.log(RedCarpet_.name);

	const _bodyRef = useRef(null);

	useEffect(() => {
		const ele: any = _bodyRef.current;
		let do2 = () => setTimeout(() => {
			ele.classList.add("step2");
		}, 500);
		let do1 = () => setTimeout(() => {
			ele.classList.add("step1");
			do2();
		}, 500);
		setTimeout(() => {
			ele.classList.add("step0");
			do1();
		}, 1000);
	});
	
	return <div id="red-carpet" ref={_bodyRef}>
		<div className="nameplate">
			<img src="./assets/img/nameplate.png" alt=""/>
		</div>
	</div>;
}

const RedCarpet = React.memo(RedCarpet_, () => true);
export default RedCarpet;
