import React, { useEffect } from 'react';
import { IonContent, IonPage } from '@ionic/react';

import './main.scss';

import ShadowStatus from "components/ShadowStatus/main";
import ShadowNav from "components/ShadowNav/main";
import ShowWindow from "components/ShowWindow/main";
import SearchBar from "components/SearchBar/main";
import SearchBar1 from "components/SearchBar1/main";
import CommonList from "components/CommonList/main";


const MainPage_: React.FC = () =>
{
	console.log(MainPage_.name);

	useEffect(() => {
		let main = document.getElementById("main-page");
		let classes = document.getElementById("search-bar1")?.classList;
		main?.addEventListener("scroll", () => {
			let top: any = main?.scrollTop;
			if (top === 0)
			{
				classes?.remove("case0");
				classes?.remove("case1");
			}
			else if (top <= 180)
			{
				classes?.remove("case1");
				classes?.add("case0");
			}
			else
			{
				classes?.remove("case0");
				classes?.add("case1");
			}
		});
	});

	return <IonPage>
		<IonContent forceOverscroll={true}>
			<div id="main-page">

				<SearchBar1 />
				<span className="scroll-line case0"></span>
				<span className="scroll-line case1"></span>

				<ShadowStatus />
				<span className="header">검색</span>
				<SearchBar />
				<span className="greeting">한국의 모든 술을<br />탐색해 보세요</span>
				<ShowWindow />
				<span className="title">지금 사람들이 가장 많이 보는 술</span>
				<CommonList />
				<ShadowNav />
			</div>
		</IonContent>
	</IonPage>
}

const MainPage = React.memo(MainPage_, () => true);
export default MainPage;
